.fixed-button {
  position: fixed;
  z-index: 15;
  right: 58px;
  bottom: 58px;
  cursor: pointer;
}

@media only screen and (max-height: 300px) {
  .fixed-button {
    display: none;
  }
}

@media only screen and (max-width: 767px) {
  .fixed-button {
    right: 10px;
    bottom: 18px;
  }
}

.fixed-button.is-open:before, .fixed-button.is-open:after {
  animation: unset;
}

.fixed-button.is-open .fixed-button__circle {
  opacity: 1;
  visibility: visible;
}

.fixed-button:before, .fixed-button:after {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  border-radius: 50%;
  animation-timing-function: linear;
}

.fixed-button:before {
  animation: pulse 1.5s infinite;
  background: rgba(3, 177, 252, 0.5);
}

.fixed-button:after {
  animation: pulse-big 1.5s infinite;
  background: rgba(3, 177, 252, 0.8);
}

.fixed-button__text {
  position: relative;
  z-index: 1;
  display: flex;
  align-items: center;
  width: 85px;
  height: 85px;
  background: #03B1FC;
  text-align: center;
  color: #fff;
  border-radius: 50%;
  font-family: 'proxima-nova-semibold', sans-serif;
  font-size: 14px;
  line-height: 1;
}

.fixed-button__text span {
  padding-top: 4px;
}

.fixed-button__circle {
  position: absolute;
  width: 200px;
  height: 200px;
  top: 50%;
  left: 50%;
  margin: -100px 0 0 -100px;
  background: #B3E9FF;
  border-radius: 50%;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.3s, visibility 0.3s;
}

.fixed-button__icon {
  position: absolute;
  display: block;
  width: 34px;
  height: 34px;
  background-size: contain;
  border-radius: 50%;
  transition: transform 0.2s ease;
  will-change: transform;
}

.fixed-button__icon:hover {
  transform: translateY(-2px);
}

.fixed-button__icon--messenger {
  top: 148px;
  left: 55px;
  background-image: url(../icons/messenger.svg);
}

@media only screen and (max-width: 767px) {
  .fixed-button__icon--messenger {
    top: 13px;
    left: 105px;
  }
}

.fixed-button__icon--instagram {
  top: 108px;
  left: 19px;
  background-image: url(../icons/instagram.svg);
}

@media only screen and (max-width: 767px) {
  .fixed-button__icon--instagram {
    top: 115px;
    left: 19px;
  }
}

.fixed-button__icon--telegram {
  top: 17px;
  left: 62px;
  background-image: url(../icons/telegram.svg);
}

@media only screen and (max-width: 767px) {
  .fixed-button__icon--telegram {
    left: 50px;
  }
}

.fixed-button__icon--viber {
  top: 53px;
