  }
}

@keyframes pulse {
  0% {
    transform: scaleX(1);
  }
  to {
    transform: scale3d(1.4, 1.4, 1.4);
    opacity: 0;
  }
}

@keyframes pulse-big {
  0% {
    transform: scaleX(1);
  }
  to {
    transform: scale3d(1.8, 1.8, 1.8);
    opacity: 0;
  }
}

.collapse {
